import React, { Component } from 'react';
import PickChamp from './PickChamp'

class PickFive extends Component {
  render() {
    const divStyle = {
      width: '100px',
      height: '500px',
      float: 'left',
      margin: '10px'
    }
    var list = [];
    var i=0;
    while(i < 5){
      list.push(
        <PickChamp
          champName={this.props.champName}
          url={this.props.url}
          key={i}
        ></PickChamp>
      );
      i=i+1;
    }
    return(
      <div style={divStyle}>
        {list}
      </div>
    );
  }
}

export default PickFive;