import React, { Component } from 'react';

class PickChamp extends Component {
  render(){
    const divStyle = {
      width: '75px',
      height: '75px',
      borderRadius: '70%',
      border: '1px solid black',
      margin: '15px'
    }
    const pickStyle = {
      width: '75px',
      height: '75px',
      borderRadius: '70%',
      overflow: 'hiddeen'
    }
    var url = this.props.url + this.props.champName + '.png'
    
    if(this.props.champName === 'Default') {
      url = 'https://www.announcementconverters.com/media/catalog/product/cache/1/image/9df78eab33525d08d6e5fb8d27136e95/I/Y/IYA11C.jpg'
    }

    return(
      <div style={divStyle}>
        <img 
          src={url}
          alt={this.props.champName}
          style={pickStyle}
        ></img>
      </div>
    );
  }
}

export default PickChamp;